.app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: Arial, sans-serif;
  }
  
  .question-container {
    text-align: center;
  }
  
  .question-text {
    font-size: 24px;
    margin-bottom: 12px;
  }
  
  .answer-section {
    display: flex;
    flex-direction: column;
  }
  
  .answer-section button {
    background-color: #4caf50;
    color: white;
    border: none;
    margin: 5px;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .answer-section button:hover {
    background-color: #45a049;
  }
  
  .score-section {
    font-size: 24px;
  }  